import React, { useState, cloneElement } from 'react';

let timerId = null;

const Accordion = ({
  children,
  config: {
    width = '100%',
    activeItemWidth = '80%',
    transitionDuration = '100ms',
  } = {},
}) => {
  const [active, setActive] = useState(-1);

  let items = [];

  if (!Array.isArray(children)) {
    items.push(children);
  } else {
    items = [...children];
  }

  items = items
    .filter((item) => item.type.name === 'AccordionItem')
    .map((item, index) => cloneElement(item, { isActive: index === active }));

  const itemsCount = items.length;

  const standartItemsWidth = `${100 / itemsCount}%`;
  const notActiveItemWidth = `${
    (100 - parseFloat(activeItemWidth)) / (itemsCount - 1)
  }%`;

  return (
    <div className='flex' style={{ width }}>
      {items.map((item, index) => {
        return (
          <div
            className='transition-all select-none'
            style={{
              width:
                active === -1
                  ? standartItemsWidth
                  : active === index
                  ? activeItemWidth
                  : notActiveItemWidth,
              transitionDuration,
            }}
            // onClick={() => setActive(index === active ? -1 : index)}
            onMouseEnter={() => {
              clearInterval(timerId);
              timerId = setInterval(() => setActive(index), 100);
            }}
            onMouseLeave={() => {
              clearInterval(timerId);
              timerId = setInterval(() => setActive(-1), 100);
              // setActive(-1);
            }}
          >
            {item}
          </div>
        );
      })}
    </div>
  );
};

export default Accordion;
